<template>
	<div class="Menu">
		<!-- 菜单管理页面 -->
		<div class="content">
			<div class="caption">
				<i class="el-icon-setting"></i>
				<span>菜单管理</span>
			</div>
			<div class="content-box">
				<el-row class="box-title">
					<el-col :span="8">菜单名称</el-col>
					<el-col :span="8">状态</el-col>
					<el-col :span="8">操作</el-col>
				</el-row>
				<template v-for="(item,index) in menu_arr">
					<template v-if="item.subs">
						<el-row class="box-details" :key="item.index">
							<el-col :span="8">
								<small class="toggle" @click="toggles(index)">
									<i :class="item.state === true ? 'el-icon-minus' : 'el-icon-plus'"></i>
								</small>
								{{item.title}}
							</el-col>
							<el-col :span="8">{{item.status === true ? '启用' : '禁用'}}</el-col>
							<el-col :span="8">
								<el-button type="primary">设置</el-button>
								<el-button :type="item.status === true ? 'warning' : 'success'" @click="toggle(index,item.state)">
									{{item.status === true ? '禁用' : '启用'}}
								</el-button>
								<el-button type="danger">删除</el-button>
							</el-col>
						</el-row>
						<template  v-if="item.state === true">
							<el-row class="box-details box-padding" v-for="(items,indexs) in item.subs" :key="items.index">
								<el-col :span="8">{{items.title}}</el-col>
								<el-col :span="8">{{items.status === true ? '启用' : '禁用'}}</el-col>
								<el-col :span="8">
									<el-button type="primary">设置</el-button>
									<el-button :type="items.status === true ? 'warning' : 'success'" @click="toggle(index,items.state,indexs)">
										{{items.status === true ? '禁用' : '启用'}}
									</el-button>
									<el-button type="danger">删除</el-button>
								</el-col>
							</el-row>
						</template>
					</template>
					<template v-else>
						<el-row class="box-details" :key="item.index">
							<el-col :span="8">{{item.title}}</el-col>
							<el-col :span="8">{{item.status === true ? '启用' : '禁用'}}</el-col>
							<el-col :span="8">
								<el-button type="primary">设置</el-button>
								<el-button :type="item.status === true ? 'warning' : 'success'" @click="toggle(index,item.state)">
									{{item.status === true ? '禁用' : '启用'}}
								</el-button>
								<el-button type="danger">删除</el-button>
							</el-col>
						</el-row>
					</template>
				</template>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data() {
			return {
				menu_arr:this.$store.state.menu,
			}
		},
		methods:{
			toggle(index,state,indexs){
				if (state != undefined) {
					let status = this.$store.state.menu[index].status;
					let flag = status === true ? false : true;
					let params = {index:index,value:flag};
					this.$store.commit('setstatus',params);
				} else{
					let status = this.$store.state.menu[index].subs[indexs].status;
					let flag = status === true ? false : true;
					let params = {index:index,value:flag,indexs:indexs};
					this.$store.commit('setstatuss',params);
				}
			},
			toggles(index){
				this.menu_arr[index].state === false ? this.menu_arr[index].state = true : this.menu_arr[index].state = false;
			}
		},
		mounted() {
			console.log(this.menu_arr[0].status)
			console.log(typeof this.menu_arr[0].status)
		}
	}
</script>

<style lang="scss" scoped>
	.Menu{
		.content {
			border: 1px solid #e7ecf1;
			padding: 0px 20px 15px;
			background-color: #fff;
		
			.caption {
				border-bottom: 1px solid #eef1f5;
				line-height: 50px;
				font-weight: 600;
				// padding: 0 10px;
				color: #2f353b;
				font-size: 14px;
		
				>i {
					font-size: 16px;
					margin-right: 6px;
					position: relative;
					top: 1px;
					font-weight: 300;
					color: #909399;
				}
			}
		
			.content-box {
				padding-top: 10px;
				.box-title{
					background: #F5F5F6;
					.el-col{
						border: 1px solid #e7e7e7;
						font-size: 13px;
						padding:0 15px;
						line-height: 40px;
						color: #333;
						border-left: 0;
					}
					>div:first-child{
						border-left: 1px solid #e7e7e7;
					}
				}
				.box-details{
					.el-col{
						border: 1px solid #e7e7e7;
						font-size: 13px;
						padding:0 15px;
						line-height: 48px;
						height: 48px;
						color: #333;
						border-top: 0;
						border-left: 0;
						>button {
							color: #FFF;
							border: 0;
							cursor: pointer;
							font-weight: 400;
							outline: none;
							padding: 6px 12px;
							font-size: 12px;
						}
					}
					.toggle{
						width: 14px;
						height: 14px;
						display: inline-block;
						border: 1px solid #ded9d9;
					    text-align: center;
						line-height: 16px;
						margin-right: 5px;
						>i{
							font-size: 10px;
							color: #ded9d9;
						}
					}
					>div:first-child{
						border-left: 1px solid #e7e7e7;
					}
				}
				.box-padding{
					>div:first-child{
						padding-left: 50px;
					}
				}
			}
		}
	}
</style>
